@import url('https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap');

/* Body Styling */
body {
  font-family: 'Patrick Hand', 'Comic Sans MS', cursive, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #F08080; /* Light coral color */
  background-image: radial-gradient(#ffffff 1px, #F08080 1px);
  background-size: 10px 10px; /* Tiny polka dot pattern */
}

/* App Container */
.App {
  text-align: center;
  padding: 20px;
  color: #333; /* Darker text color for contrast */
}

/* Headers */
h2, h3, h4 {
  margin: 20px 0;
  color: #ffffff; /* White color for better visibility on coral background */
}

/* Inputs */
input[type="text"], input[type="number"] {
  padding: 10px;
  margin: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 200px;
  font-size: 16px;
  font-family: 'Patrick Hand', 'Comic Sans MS', cursive, sans-serif;
}

/* Buttons */
button {
  padding: 10px 20px;
  margin: 10px;
  border: none;
  border-radius: 8px;
  background-color: #ff9900;
  color: white;
  font-size: 16px;
  font-family: 'Patrick Hand', 'Comic Sans MS', cursive, sans-serif;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #985e06;
}

/* Lists */
ul {
  list-style: none;
  padding: 0;
}

li {
  margin: 10px 0;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

ul {
  list-style: none;
  padding: 0;
}

li {
  margin: 10px 0;
}
